export default {
	data() {
		return {
			avatarUrl: require("@/assets/images/avatar.jpg"),
			pay:{
				label: "充值",
				value: "error"
			}
		};
	},

	methods: {
		chooseImage() {
			uni.chooseImage({
				count: 1,
				success: res => {
					this.$refs["cs-list"].restore();
					this.avatarUrl = res.tempFilePaths[0];
				}
			});
		}
	}
};
